@font-face {
    font-family: 'Rubik';
    font-weight: normal;
    font-style: normal;
    src: url(./fonts/rubik.woff2) format('woff2'), url(./fonts/rubik.woff) format('woff');
}

html {
    font-size: 16px;
}

body {
    font-family: "Rubik";
    margin: 0;
    background-color: #D8EEFE;
}

h1 {
    font-size: 64px;
    line-height: 76px;
    color: #094067;
}

ul {
    padding: 0;
    list-style: none;
    margin: 0;
}

div.container {
    padding: 15px;
}

button {
    outline: none;
    border: none;
    cursor: pointer;
}

.parent {
    display: flex;
    flex-direction: column;
    gap: 31px;
    padding-top: 47px;
    .list_container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        background-color: #FFFFFE;
        padding-top: 22px;
        padding-left: 43px;
        padding-right: 25px;
        padding-bottom: 22px;
        border-radius: 5px;
        img {
            width: 92px;
            height: 94px;
            border-radius: 5px;
        }
        .list_item {
            display: flex;
            flex-direction: column;
        }
    }
    .button_container {
        display: flex;
        flex-direction: row;
        gap: 22px;
        padding-top: 38px;
        button {
            background: none;
            svg {
                height: 20px;
                width: 27px;
            }
        }
        .delete svg {
            color: red;
        }
    }
}

.section_filtering {
    display: flex;
    flex-direction: column;
    gap: 20px;
    .filtering_form {
        display: flex;
        flex-direction: column;
    }
    label {
        font-weight: 500;
        font-size: 18px;
        line-height: 21px;
        color: #094067;
        padding-bottom: 13px;
    }
    select {
        padding-right: 20px;
    }
    .filtering_input {
        font-weight: 500;
        font-size: 18px;
        line-height: 21px;
        color: #5F6C7B;
        padding-left: 20px;
        padding-top: 15px;
        padding-bottom: 15px;
        border-radius: 5px;
        outline: none;
        border: none;
    }
    .add_btn {
        font-weight: 500;
        font-size: 18px;
        line-height: 21px;
        color: #FFFFFE;
        padding: 15px 18px;
        background: #EF4565;
        cursor: pointer;
        outline: none;
        border: none;
        border-radius: 5px;
        margin-top: 12px;
    }
}

.popup {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: fixed;
    background: rgba(255, 255, 255, 0.8);
    .content {
        // margin-top: 169px;
        // margin-bottom: 112px;
        // margin-left: 180px;
        // margin-right: 180px;
        background-color: white;
        padding: 3rem;
        // padding-top: 121px;
        // padding-right: 298px;
        // padding-bottom: 189px;
        // padding-left: 265px;
        fieldset {
            display: flex;
            flex-direction: column;
            border: none;
            label {
                padding-top: 11px;
                padding-bottom: 12px;
            }
            .btn_container {
                padding-top: 63px;
                display: flex;
                flex-direction: row;
            }
        }
    }
}

