.reminder-par {
    font-weight: 500;
    font-size: 38px;
    line-height: 47px;
    color: #094067;
    padding: 0;
    padding-bottom: 26px;
    margin: 0;
}

.form input,
.form label {
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    color: #094067;
}

.form input {
    padding-left: 20px;
    padding-top: 15px;
    padding-bottom: 14px;
    background: #D8EEFE;
    border-radius: 5px;
    border: none;
    outline: none;
}

.form label {
    padding-bottom: 8px;
    padding-top: 11px;
}

.form li {
    display: flex;
    flex-direction: column;
}

.popup-btn-container {
    padding-top: 10px;
    display: flex;
    flex-direction: row;
}

.popup-btn-container button {
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    border-radius: 5px;
    padding-top: 12px;
    padding-bottom: 11px;
    padding-left: 20px;
    padding-right: 16px;
}

.delete-popup-content {
    top: 20%;
}

.delete-popup-content .reminder-par {
    font-size: 20px;
    line-height: 25px;
}

.delete-popup-content ul {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
}

button.submit {
    background: #EF4565;
    color: #FFFFFE;
}

button.cancel {
    color: #5F6C7B;
    background: #FFFFFF;
    border: 2px solid #5F6C7B;
    box-sizing: border-box;
    border-radius: 5px;
    margin-left: 30px;
}

button.cancel:hover {
    background: #EF4565;
    color: #FFFFFE;
}

.delete-popup-content .yes {
    background-color: blue;
    color: #FFFFFF;
}

.delete-popup-content button {
    padding: 12px 18px;
}

@media(min-width:768px) {
    .section_filtering {
        display: grid;
        grid-auto-flow: column;
        /* grid-template-columns: repeat(3, minmax(100px, 1fr)); */
        place-items: center center;
        gap: 20px;
        align-items: end;
    }
    .filtering_form {
        margin-right: 0;
    }
}

@media(min-width: 1114px) {
    div.container {
        max-width: 1114px;
        margin: auto;
    }
}